import React,{Component} from 'react'
import  "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import Cons from './cons';
import Improve from './improve';
import './main.css';
import Pros from './pros'
import Ronin from './roninracing';
import Foot from './footer';

class Main extends Component{
    render(){
        return(
            <div className="wid" id="wid">
                <div className="row headercolor wid">
                    <div className="col-2 headitem">
                        <a href="#wid" ><button className="btn btn-primary">Home</button></a>
                    </div>
                    <div className="col-2 headitem">
                        <a href="#Ronin"><button className="btn btncolor"><a>RONIN</a></button></a>
                    </div>
                    <div className="col-2 headitem">
                        <a href="#Improve"><button className="btn btncolor"><a>IMPROVES</a></button></a>
                    </div>
                    <div className="col-2 headitem">
                    <a href="#pros"><button className="btn btncolor"><a>PROS</a></button></a>
                    </div>
                    <div className="col-2 headitem">
                        <a href="#cons"><button className="btn btncolor"><a>CONS</a></button></a>
                    </div>
                    <div className="col-2 headitem ">
                        <a href="#About"><button className="btn btncolor"><a>ABOUT</a></button></a>
                    </div>
                </div>
                <div >
                <br/><br/>
                    <div id="Ronin"><br/><br/><br/><br/><Ronin /></div>
                    <div id="Improve" className="bg1"><br/><br/><br/><br/><Improve/></div><br/><br/><br/>
                    <div id="pros" className="bg2"><br/><br/><br/><br/><br/><br/><br/><br/><Pros/></div><br/><br/><br/>
                    <div id="cons" className="bg3"><br/><br/><br/><br/><br/><br/><br/><br/><Cons /></div>
                    <div id="About"><br/><br/><br/><br/><Foot/></div>
                </div>
            </div>
        );
    }
}

export default Main