import React,{Component} from 'react'
import  "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import './main.css';
import logos from './evlogo.png'

class Ronin extends Component{
    render () {
        return(
        <div>
            <div className="container">
                <div className="row">
                <img src={logos} className="imge"/>
            </div>
                <br/><br/><br/>
                <div className="row left">
                    <h1>Future of Electric Sports Car : </h1>
                </div><br/>
                <div className="row left">
                    <div className="col-1"></div>
                 <div className="col-10">Ronin Racing is the first formula student electric vehicle team of National Institute of Technology, Warangal. The team comprises undergraduate students determined to apply their engineering knowledge to design and fabricate a formula style electric car and participate in Formula Student Electric and equivalent competitions</div>
                 </div>
                <br/>
                <div className="row left">
                <h1>Aim : </h1>
                    <div className="col-1 "></div>
                    <div className="col-10">
                        To form a community of students of NIT Warangal, motivated to explore the field of hybrid and electric vehicles
                    </div>
                </div>
                <br/><br/>
                <div className="row left">
                <h1>Objectives : </h1>
                    <div className="col-1 "></div>
                    <div className="col-10">
                        <ul>
                            <li>Create awareness about trends in the EV industry</li>
                            <li>Gain technical understanding,acquire skills to design EV subsystems</li>
                            <li>Disseminate the knowledge gained to a wider audience</li>
                            <li>Provide students with a better standing in the emerging EV industry</li>
                        </ul>
                    </div>
                </div>
                <br/><br/>
            </div>
        </div>
        );
    }
}
export default Ronin